import "./ResultGender.css";
import StatsBar from "../../../Stats/StatsBar/StatsBar";
import ResultBackground from "../ResultBackground/ResultBackground";

function ResultGender({ result }) {
  if (!result) return;

  return (
    <ResultBackground>
      <div className="result-gender-wrapper">
        <div className="result-man-wrapper">
          <h4 className="result-man-title"><span className="stroke"></span></h4>
          <StatsBar choiceA={result.man.A} choiceB={result.man.B} />
          <StatsBar choiceA={result.man["40A"]} choiceB={result.man["40B"]} />
          <StatsBar choiceA={result.man["30A"]} choiceB={result.man["30B"]} />
          <StatsBar choiceA={result.man["20A"]} choiceB={result.man["20B"]} />
          <StatsBar choiceA={result.man["10A"]} choiceB={result.man["10B"]} />
        </div>
        <div className="result-gender-title-wrapper">
          {["", "40", "30", "20", "10"].map((title) => (<span className="stroke">{title}</span>))}
        </div>
        <div className="result-woman-wrapper">
          <h4 className="result-woman-title"><span className="stroke"></span></h4>
          <StatsBar choiceA={result.woman.A} choiceB={result.woman.B} />
          <StatsBar choiceA={result.woman["40A"]} choiceB={result.woman["40B"]} />
          <StatsBar choiceA={result.woman["30A"]} choiceB={result.woman["30B"]} />
          <StatsBar choiceA={result.woman["20A"]} choiceB={result.woman["20B"]} />
          <StatsBar choiceA={result.woman["10A"]} choiceB={result.woman["10B"]} />
        </div>
      </div>

    </ResultBackground>
  )
}

export default ResultGender