import "./Login.css";
import { useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import useLogin from "../../hooks/useLogin";
import Button from "../Button/Button";

function Login() {
  const [inputs, setInputs] = useState({
    id: "",
    password: ""
  })

  const auth = useSelector(state => state.auth);

  const { userLogin, isPending, error } = useLogin();
  const navigate = useNavigate();

  const inputHandler = (event) => {
    setInputs({
      ...inputs,
      [event.target.name]: event.target.value
    })
  }

  const loginHandler = async (event) => {
    const { id, password } = inputs;

    await userLogin(id, password);
  }

  if (auth.isLogin || auth.user) return;

  else return (
    <form className="login-wrapper" onChange={inputHandler}>
      <div className="login-background"></div>
      <p className="login-notice stroke">   <br />   <br />   .</p>
      <input type="text" name="id" placeholder="" />
      <input type="password" name="password" placeholder="" />
      <p className="login-error-message">{error}</p>
      <Button size="small"
        background="var(--color-button-a)"
        text=""
        disabled={isPending}
        onClick={loginHandler} />
      <Button size="xSmall"
        background="var(--color-button-c)"
        text=""
        onClick={() => navigate("/signup")} />
    </form>
  )
}

export default Login;